import React from 'react';

function CareScale({ scaleValue, careType }) {
    // D√©clare un tableau de valeurs de r√©f√©rence pour le niveau de soin (1, 2, 3)
    const range = [1, 2, 3];
    
    // D√©termine le type d'ic√¥ne √† afficher en fonction du type de soin : '‚òÄÔ∏è' pour 'light', 'üíß' pour 'water'
    const scaleType = careType === 'light' ? '‚òÄÔ∏è' : 'üíß';

    return (
        <div>
            {/* Utilise map() pour parcourir le tableau range et afficher l'ic√¥ne en fonction du scaleValue */}
            {range.map((rangeElem) =>
                // Si scaleValue est sup√©rieur ou √©gal √† rangeElem, affiche l'ic√¥ne scaleType
                scaleValue >= rangeElem ? 
                    <span key={rangeElem.toString()}>{scaleType}</span> 
                    : 
                    // Si scaleValue est inf√©rieur, ne rien afficher (null)
                    null
            )}
        </div>
    );
}


export default CareScale