import { useState } from "react";
import { Routes, Route } from 'react-router-dom';
import React from 'react';
import Banner from "./Banner";
import Cart from "./Cart";
import ShoppingList from "./ShoppingList";
import Footer from "./Footer";
import PlantDetail from "./plantDetails/PlantDetail.js";
import PlantPaye from "./plantPaye/PlantPaye.js";
import Login from "./Login.js";


function App(){
  const [cartItems, setCartItems] = useState([]);
  const [isCartActive, setIsCartActive] = useState(false);
  const [isLoginVisible, setIsLoginVisible] = useState(false);
  const [isConnectActive, setIsConnectActive] = useState(true);

  const addToCart = (name, price, image) => {
    setCartItems((prevItems) => {//prevItems repr√©sente la version actuelle de cartItems (le panier avant l‚Äôajout de la nouvelle plante).
      const existingItem = prevItems.find((item) => item.name === name);//find() parcourt prevItems pour v√©rifier s'il existe un article dont le name correspond √† la plante ajout√©e. Si une plante avec le m√™me name est d√©j√† pr√©sente dans prevItems, elle est stock√©e dans existingItem. Si la plante n'existe pas encore, existingItem sera undefined.
      if(existingItem){
        return prevItems.map((item) =>
        item.name === name ? {...item, quantity: item.quantity + 1} : item);//map() est utilis√© pour cr√©er une nouvelle version de prevItems, o√π chaque √©l√©ment est v√©rifi√©. Pour l'√©l√©ment correspondant √† la plante (c‚Äôest-√†-dire item.name === name), on cr√©e un nouvel objet avec les m√™mes propri√©t√©s (...item) mais avec quantity incr√©ment√© de 1 (quantity: item.quantity + 1). Les autres √©l√©ments du panier sont laiss√©s inchang√©s (: item).
      }else{
        return [...prevItems, {name, price, image, quantity: 1}];//Si la plante n‚Äôest pas d√©j√† dans le panier, elle est ajout√©e en tant que nouvel √©l√©ment. ...prevItems ajoute tous les √©l√©ments existants, puis { name, price, quantity: 1 } ajoute la nouvelle plante avec une quantit√© initiale de 1.
      }
    });
  };

  const addToCartDetail = (name, price, quantity, image) => {
    setCartItems((prevItems) => {//prevItems repr√©sente la version actuelle de cartItems (le panier avant l‚Äôajout de la nouvelle plante).
      const existingItem = prevItems.find((item) => item.name === name);//find() parcourt prevItems pour v√©rifier s'il existe un article dont le name correspond √† la plante ajout√©e. Si une plante avec le m√™me name est d√©j√† pr√©sente dans prevItems, elle est stock√©e dans existingItem. Si la plante n'existe pas encore, existingItem sera undefined.
      if(existingItem){
        return prevItems.map((item) =>
        item.name === name ? {...item, quantity: item.quantity + quantity} : item);//map() est utilis√© pour cr√©er une nouvelle version de prevItems, o√π chaque √©l√©ment est v√©rifi√©. Pour l'√©l√©ment correspondant √† la plante (c‚Äôest-√†-dire item.name === name), on cr√©e un nouvel objet avec les m√™mes propri√©t√©s (...item) mais avec quantity incr√©ment√© de 1 (quantity: item.quantity + 1). Les autres √©l√©ments du panier sont laiss√©s inchang√©s (: item).
      }else{
        return [...prevItems, {name, price, quantity, image}];//Si la plante n‚Äôest pas d√©j√† dans le panier, elle est ajout√©e en tant que nouvel √©l√©ment. ...prevItems ajoute tous les √©l√©ments existants, puis { name, price, quantity: 1 } ajoute la nouvelle plante avec une quantit√© initiale de 1.
      }
    });
  };

  const clearCart = () => {
    setCartItems([]);
  };

  const clearElement = (name) => {
    setCartItems((prevItems) => {
      return prevItems.map((item) =>
        item.name === name ? {...item, quantity: item.quantity - 1} : item) // D√©cr√©mente la quantit√© si la plante est trouv√©e
        .filter((item) => item.quantity > 0); // Retire l'√©l√©ment du tableau si sa quantit√© tombe √† 0
    });
  };

  const toggleCart = () => {
    setIsCartActive((prevState) => !prevState);
  }

  const toggleConnect = () => {
    setIsConnectActive((prevState) => !prevState);
  }

  const showLogin = () => setIsLoginVisible(true);
  const hideLogin = () => setIsLoginVisible(false);

  return (
    <div>
      <Banner toggleCart={toggleCart} />
      <div className="main">
        <Cart 
          cartItems={cartItems} 
          clearCart={clearCart} 
          clearElement={clearElement} 
          isCartActive={isCartActive}
          showLogin={showLogin}/>
        <Routes>
          <Route path="/" element={<ShoppingList addToCart={addToCart} />} />
          <Route path="/ShoppingList" element={<ShoppingList addToCart={addToCart} />} />
          <Route path="/plant/:id" element={<PlantDetail addToCartDetail={addToCartDetail} />}/>
          <Route path="/PlantPaye" element={<PlantPaye cartItems={cartItems} clearElement={clearElement} clearCart={clearCart}/>}/>
        </Routes>
      </div>
      {isLoginVisible && <Login onClose={hideLogin} isConnectActive={isConnectActive} toggleConnect={toggleConnect}/>}
      <Footer />  
    </div> 
  );

}

export default App;
